<template>
<div style="bg-admin">
<vue-headful
    title="FANPAGE OR PROFILE || Ci t ti khon OpenLive"
/>
<b-container fluid>
<b-row>
<!-- Sidebar custom left -->
  <b-col cols="2" style="padding-right:0px;padding-left:0px" v-if="isActive">
      <sidebarDashboard/>
  </b-col>
<!-- Dashboard right -->
<b-col :cols="sidebarSize" style="padding-right:0px;padding-left:0px">
  <navbarDashboard @toggleSideBar="isActiveInfo()"/>
  <b-col cols="12">
  
  <h2 style="color:orange" class="mt-2 ml-2">CI T TI KHON LIVE</h2>
   <b-card style="border-radius:0px" class="mt-2">
    <div>   
        <b-col sm="12" md="8" offset-md="2">
            <b-form @submit="onSubmit">
            <vue-snotify></vue-snotify>
                <p>{{ selectTypeAccountFanpageLive }}</p>
                <h5 style="color: orange;font-size: 17px;">CHN TI KHON KHI LIVE</h5>
                <span style="color: #7a7a7a;"> Bn live bng Facebook C Nhn hay Fanpage ?? <i style="color:orange" v-b-popover.hover.top="'Bn s dng ng dng ca iBee.Vn LIVE  Trang c nhn hay  FanPage?? Nu bn LIVE  Trang C Nhn th hy chn Trang C Nhn nh!'" class="fa fa-question-circle"/></span>
                <b-form-select v-model="selectTypeAccountLive" :options="optionsTypeAccountLive" class="mb-3" />
                <!--@change="getTypeAccountLive(selectTypeAccountLive)"-->
                <div v-if="isActiveSelectFanpage">
                <h5 style="color: orange;font-size: 17px;">CHN TN FANPAGE BN LIVE</h5>
                <span style="color: #7a7a7a;"> Bn live bng Fanpage no ?? <i style="color:orange" v-b-popover.hover.top="'Bn s dng ng dng ca iBee.Vn LIVE  FanPage?? Vy hy chn Fanpage chnh m bn hay LIVE Bn hng trn !'" class="fa fa-question-circle"/></span>
                 <b-form-select v-model="selectTypeAccountFanpageLive" class="mb-3">
                  <template slot="first">
                    <!-- this slot appears above the options from 'options' prop -->
                    <option :value="null" disabled>Hy chn Fanpage bn LIVE</option>
                  </template>
                  <!-- these options will appear after the ones from 'options' prop -->
                   <option v-for="item in optionsTypeAccountFanpageLive" :value="item.id">{{ item.name }}</option>
                </b-form-select>
                </div>
                <h5 style="color: orange;font-size: 17px;">CHN M SN PHM</h5>
                <span style="color: #7a7a7a;"> Bn mun dng m sn phm nhp sn hay random m cho bn ?? <i style="color:orange" v-b-popover.hover.top="'Khi bn LIVE th iBee.Vn s hin th M S cho bn c cho KHCH HNG. Nu bn c d liu sn trong iBee.Vn th c th s dng M S t h thng sn phm. Hoc nu khng th chn NGU NHIN nh!'" class="fa fa-question-circle"/></span>
                <b-form-select v-model="selectTypeCodeProduct" :options="optionsTypeCodeProduct" class="mb-3" />

                <h5 style="color: orange;font-size: 17px;">CHN TH LOI SN PHM</h5>
                <span style="color: #7a7a7a;"> Bn bn nhiu sn phm hay ch 1 sn phm ?? <i style="color:orange" v-b-popover.hover.top="'Nu bn ch bn 1 sn phm duy nht, nh kiu (Bng in tch in). Th bn nn chn MT LOI SN PHM.Cn nu bn bn qun o hay ng h nhiu m th nn chn NHIU SN PHM nh!'" class="fa fa-question-circle" /></span>
                <b-form-select v-model="selectTypeProductShop" :options="optionsTypeProductShop" class="mb-3" />

                <h5 style="color: orange;font-size: 17px;">CHN S KHCH HNG D B</h5>
                <span style="color: #7a7a7a;">  Nu khch hng u t chi mua sn phm. Bn cn khch hng thay th ?? <i style="color:orange" v-b-popover.hover.top="'Nu nh trong khi 1 v khch ca bn  cht n hng trong qu trnh LIVE bn hng.. Nhng sau  h khng mua na th mc nh iBee.Vn  lu tr cho bn 2 v khch hng d b  thay th n hng ri.Nu bn mun d b nhiu hn th hy thay th cho ph hp nh!'" class="fa fa-question-circle"/></span>
                <b-form-group id="exampleInputGroup1"
                               >
                    <b-form-input id="exampleInput1"
                                type="text"
                                value="2"
                                required
                                v-model="userReserve"
                                placeholder="Nhp s ngi d b">
                    </b-form-input>
                </b-form-group>

                <b-button type="submit" style="color:#FFF;cursor:pointer" variant="warning">LU CI T</b-button>
      </b-form>
        </b-col>
    </div>
    </b-card>
  </b-col>
</b-col>
  </b-row>
</b-container>
</div>
</template>

<script>
import axios from 'axios'
import navbarDashboard from '@/components/layout/admin/navbarDashboard'
import sidebarDashboard from '@/components/layout/admin/sidebarDashboard'
export default {
  name: 'appPage',
  components: {
    navbarDashboard,
    sidebarDashboard
  },
  data () {
    return {
      // Toggle Sidebar
      isActive: true,
      sidebarSize:'10',
      loadLive: true,
      isActiveSelectFanpage: false,
      
      selectTypeAccountLive: 'profile',
      optionsTypeAccountLive: [
        { value: 'profile', text: 'Trang C Nhn' },
        { value: 'fanpage', text: 'Trang Fanpage' }
      ],
      
      selectTypeCodeProduct: 'randomType',
      optionsTypeCodeProduct: [
        { value: 'randomType', text: 'Ngu nhin' },
        { value: 'productType', text: 'M Sn Phm c lp sn' }
      ],

      selectTypeProductShop: 'multipleProduct',
      optionsTypeProductShop: [
        { value: 'multipleProduct', text: 'Nhiu sn phm' },
        { value: 'singleProduct', text: 'Mt loi sn phm' }
      ],

      selectTypeAccountFanpageLive: null,
      optionsTypeAccountFanpageLive: [],
      userReserve: '2'
    }
  },
  created (){
    var _this = this;
    // Kim tra ng nhp
      if(!this.$session.get('authUser')){
          this.$router.push('/')
      }else{
        if(this.$session.get('authUser').role_id !== '0'){
          this.$router.push('/admin')
        }
      }
      // Hin th thng bo khi mi vo!
     window.setTimeout(()=>{ 
      _this.displayNotification();
      }, 2000);   
  },
  watch: {
    selectTypeAccountLive: function(val) {
      this.isActiveSelectFanpage = (val === 'fanpage');
    },
    isActiveSelectFanpage: function(val) {
      const access_token = this.$session.get('access_token');
      const _this = this;
      if (val) {
       FB.api(`me/accounts?access_token=${access_token}`, (response) => {
              const dataFanpage = response.data;
              if(dataFanpage.length == 0){
                _this.isActiveSelectFanpage = false;
                _this.selectTypeAccountLive = 'profile'
                _this.displayNotificationError();
              }else{
                _this.optionsTypeAccountFanpageLive = response.data;
              }
        })
      }
    }
  },
  methods: {
    onSubmit(event){
     event.preventDefault();
     console.log(' gi thng tin');
     console.log(`Ti khon: ${this.selectTypeAccountLive} - Tn Fanpage: ${this.selectTypeAccountFanpageLive} - 
     Kiu m: ${this.selectTypeCodeProduct} - Loi sn phm: ${this.selectTypeProductShop} - Khch d b : ${this.userReserve}`)
    },
    displayNotification() {
        this.$snotify.html(`<div class="snotifyToast__title" style="color:orange;font-size:20px"><b>Tips cho bn!</b></div>
        <div class="snotifyToast__body">Nu bn khng hiu cc  la chn c th click vo <i style="color:orange" class="fa fa-question-circle"></i>  bit thm thng tin nh!</div> `, {
        timeout: 10000,
        showProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
      });
    },
    displayNotificationError() {
        this.$snotify.error('Li bn khng c Fanpage no c!','Thng bo', {
        timeout: 10000,
        showProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true
      });
    },
      // Config Toggle SideBar
      isActiveInfo(){
        this.isActive = !this.isActive;
        if(this.isActive !== true){
          this.sidebarSize = 12;
        }else{
          this.sidebarSize = 10;
        }
      },
      // ng xut
      logOut(){
      this.$session.destroy()
      this.$router.push('/')
    }
  }
}
</script>

<style scoped>


.scrollDiv{
      height: 300px;
      position:relative;
      overflow-y: scroll;
}
</style>


